// 物品 Schema
const ItemSchema = z.object({
    名称: z.string().prefault(''),
    描述: z.string().prefault(''),
    品阶: z.string().prefault(''),
    数量: z.coerce.number().transform(v => Math.max(1, v)).prefault(1), // 默认为 1 且最小为 1，避免被过滤
});
// 宗门势力 Schema - 优化版
const FactionSchema = z.object({
    地: z.string().describe('所在地'),
    特: z.string().describe('核心特点'),
    力: z.string().describe('最高战力'),
    营: z.enum(['正', '魔', '中']),
    模: z.enum(['超大', '大', '小', '微', '特']),
});
// 功法 Schema - 优化版
const TechniqueSchema = z.object({
    阶: z.enum(['凡', '黄', '玄', '地', '天', '仙', '圣']),
    性: z.string().describe('属性'),
    效: z.string().describe('效果'),
});
// 法宝兵器 Schema - 优化版
const TreasureSchema = z.object({
    阶: z.enum(['凡器', '法器', '灵器', '法宝', '灵宝', '仙器', '圣器', '道器']),
    类: z.string().describe('类型'),
    特: z.string().describe('特性'),
});
// 地点 Schema - 优化版
const LocationSchema = z.object({
    域: z.enum(['天层', '神州', '东苍', '南炎', '西庚', '北冥', '下层', '四海']),
    类: z.enum(['秘境', '城镇', '宗门', '禁地', '遗迹', '地形']),
    危: z.coerce.number().transform(v => _.clamp(v, 0, 100)),
    特: z.string().describe('特点'),
    资: z.array(z.string()).prefault([]),
});
// 灵根 Schema - 优化版
const SpiritRootSchema = z.object({
    质: z.enum(['劣', '下', '中', '上', '极', '天', '异']),
    性: z.string().describe('属性'),
    速: z.string().describe('修炼倍率'),
    特: z.string().describe('特性'),
    稀: z.enum(['常', '少', '罕', '稀', '传']),
});
// 体质 Schema - 优化版
const PhysiqueSchema = z.object({
    质: z.enum(['凡', '灵', '道', '圣', '神']),
    特: z.string().describe('特性'),
    优: z.string().describe('优势'),
    稀: z.enum(['常', '少', '罕', '稀', '传']),
});
